@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
}

ul,
li,
a {
  text-decoration: none;
  list-style-type: none;
  color: #000000;
  margin: 0;
  padding: 0.75rem;
}
button {
  color: #000000;
}

/*************
    HEADER
*************/

.fade-enter {
  position: fixed;
  height: 30%;
  width: 100%;
  top: 0;
  left: 0;
  overflow-y: hidden;
  z-index: 1;
  padding: 1.5rem 0;
  font-size: 2rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translateY(-250px);
  background: #ffffff;
}
.fade-enter-done {
  position: fixed;
  height: 230px;
  width: 100%;
  top: 0;
  left: 0;
  overflow-y: hidden;
  z-index: 1;
  padding: 1.5rem 0;
  font-size: 2rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffffff;
  transform: translateY(0);
  transition: all 100ms ease-in-out;
}

.fade-exit {
  position: fixed;
  height: 30%;
  width: 100%;
  top: 0;
  left: 0;
  overflow-y: hidden;
  z-index: 1;
  padding: 1.5rem 0;
  font-size: 2rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffffff;
  transform: translateY(0);
}

.fade-exit-active {
  position: fixed;
  height: 30%;
  width: 100%;
  top: 0;
  left: 0;
  overflow-y: hidden;
  z-index: 1;
  padding: 1.5rem 0;
  top: 0%;
  font-size: 2rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translateY(-250px);
  background: #ffffff;
  transition: all 100ms ease-in-out;
}

.header-container {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 1rem;
  width: 100%;
  height: 135px;
  background: #ffffff;
  z-index: 30;
}

.hamburger-menu {
  position: fixed;
  border: none;
  padding: 6px;
  top: 2.5rem;
  right: 1rem;
  font-size: 2rem;
  z-index: 100;
  background: none;
}

.header-button,
.main-button {
  position: relative;
  font-weight: 700;
  top: -6px;
  left: 6px;
  color: #000000;
  background: #ffffff;
  border: 2px solid #000000;
  border-radius: 6px;
  box-shadow: -6px 6px 0 0 #000000;
  padding: 12px 20px 14px 20px;
}

.header-button {
  font-size: 2rem;
  margin: 0;
}

/*************
    UTILITY
*************/

.main-button {
  position: unset;
  font-size: 1.25rem;
  margin-top: 1rem;
  font-weight: 600;
  z-index: 0;
}

.list-container {
  padding: 1rem;
}

.centering {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/*************
    HOME
*************/

.hello-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 3rem 1rem;
  padding-top: 5rem;
  line-height: 2rem;
}

.landing-container {
  margin: 4rem 0;
}

.home-about {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(230, 230, 230);
  border-radius: 10px 10px 0 0;
  min-height: 50vh;
  margin: 0 1rem;
}

.home-heading {
  font-size: 2rem;
  font-weight: 500;
  padding-top: 3rem;
  padding-bottom: 1rem;
}

.home-title {
  font-size: 2rem;
  padding-bottom: 1rem;
}

.line {
  width: 50%;
  height: 2px;
  background: black;
  margin-bottom: 3rem;
}

/******************************
    HOME - DESCRIPTION CARDS
*******************************/

.description-container {
  display: flex;
  flex-direction: column;
}

.description-icon {
  padding: 1rem 0;
  font-size: 3rem;
}

.description-card {
  background: #e0e0e0;
  border: 2px solid #000000;
  border-radius: 6px;
  box-shadow: 6px -6px 0 0 #000000;
  padding: 6px 20px 14px 20px;
  margin: 1rem 0;
  max-width: 20rem;
}

.description-card:hover {
  background: #000000;
}

.descriptions {
  padding: 1rem;
  font-weight: 400;
}

.color-red:hover {
  background: #ecb6b6;
}

.color-blue:hover {
  background: #b6d8ec;
}

.color-green:hover {
  background: #b6ecb6;
}

/************************
    HOME - PROJECT CARDS
*************************/

.project-cards {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.project-card {
  position: relative;
  max-width: 100%;
  max-height: 250px;
  margin: 0.5rem 0;
}

.project-caption {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project-card-title {
  font-size: 1.5rem;
  font-weight: 600;
}

.view-button {
  width: 100%;
  background: #ffffff;
  font-size: 1.25rem;
  padding: 0 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.view-button h3 {
  font-weight: 500;
  padding: 1rem;
}

.project-caption-enter {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #e0e0e0;
  opacity: 0;
}

.project-caption-enter-done {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #e0e0e0;
  opacity: 0.95;
  transition: opacity 200ms;
}
.project-caption-exit {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #e0e0e0;
  opacity: 1;
}

.project-caption-exit-active {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #e0e0e0;
  opacity: 0;
  transition: opacity 300ms;
}

.project-card-image {
  max-width: 100%;
  height: 250px;
  object-fit: cover;
}

/*************
    CONTACT
*************/

.contact-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 1rem;
  line-height: 2rem;
  background: #202020;
  color: #ffffff;
}

.social-buttons a {
  color: #ffffff;
}

.line-contact {
  width: 50%;
  height: 2px;
  background: #ffffff;
  margin-bottom: 3rem;
}

/**************
    PORTFOLIO
***************/

.portfolio-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 3rem 2rem;
  padding-top: 10rem;
  line-height: 2rem;
}

.portfolio-title {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.portfolio-title h2 {
  font-size: 2rem;
}

.project-line {
  width: 150%;
  height: 2px;
  background: black;
  margin-top: 1rem;
  margin-bottom: 3rem;
}

.project-content-title {
  margin: 2rem;
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: 500;
}

.image-container {
  width: 100%;
}

.project-content-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 4rem;
  align-items: center;
  text-align: left;
}

.project-content-container span {
  font-size: 1.1rem;
}

.project-button {
  position: relative;
  font-weight: 700;
  top: -6px;
  left: 6px;
  color: #000000;
  background: #ffffff;
  border: 1px solid #000000;
  border-radius: 6px;
  box-shadow: -6px 6px 0 0 #000000;
  padding: 12px 20px 14px 20px;
  margin: 1rem;
  text-align: center;
}
.project-content-para {
  margin: 1rem 0;
}

/************
    SKILLS
*************/

.about-skills {
  padding: 2rem 0;
}

.about-heading {
  padding-top: 4rem;
}

.about-bold {
  font-weight: 600;
}
